import React, { Component } from 'react'
import Navbar from './Navbar.js'
import 'bulma/css/bulma.css'
import '../App.css'



class Rules extends Component {
    constructor(props){
        super(props)
        this.store = props.store
    }

    render(){
        const timer = (props) =>{
            let {
                days,
                hours,
                minutes,
                seconds
            } = props.formatted
            return(
                <div className="container has-text-centered">
                  <h1 className="title is-size-1 timer has-text-warning has-text-weight-bold is-family-monospace">
                    {days} : {hours} : {minutes} : {seconds}
                  </h1>
                </div>   
            )
        }
        return (
        <div >
          <section className="hero is-fullheight-with-navbar bg-img" style={{height:"10%"}}>
            <Navbar activeTab={'rules'}/>
            <div className="hero-body is-centered has-text-centered columns">
                <div className="column is-6 box ">
                    <a target="_blank" href="https://docs.google.com/document/d/1LpwSh9a3MY4nOU9SwXMnpxcMdPr1m3iMXTLobIPrHL8/edit?usp=sharing">Rules and Guidelines</a>
                </div>
            </div>

        </section>
        </div>
    )}
    
}

export default Rules



