import React from 'react'
import { Link } from 'react-router-dom'
import cn from 'classnames'
import { slide as Menu } from 'react-burger-menu'
const Navbar = (props) => {

    return(
        <Menu>
                <a className={cn("",  {
                    "has-text-warning" : props.activeTab === 'home'
                })} href="/si" style={{'fontWeight':'bold', 'fontSize': '16px'}}> Home </a>
                <a className={cn("",  {
                    "has-text-warning" : props.activeTab === 'facts'
                })} href="/si/#/facts/" style={{'fontWeight':'bold', 'fontSize': '16px'}}> F.A.C.T.S </a>
                <a className={cn("",  {
                    "has-text-warning" : props.activeTab === 'rules'
                })} href="/si/#/rules/" style={{'fontWeight':'bold', 'fontSize': '16px'}}> Rules </a>
        </Menu>
)
}

export default Navbar


